.btn
	@apply cursor-pointer select-none transition-all duration-300
	&.loading
		@apply bg-transparent #{!important}
		span,.icon
			@apply opacity-0
		&::before
			content: ''
			animation-duration: 0.5s
			@apply w-5 h-5 border-3 border-white/40 border-t-white rounded-full inline-block
			@apply absolute animate-spin
	&.btn-sw-1
		@apply sq-12 py-2 px-5 rounded-full  flex-center 
		@apply bg-transparent 
		&::before
			@apply font-awesome font-light text-Primary-4 body-2
		&::after
			-webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0)
			-webkit-mask-composite: xor
			position: absolute
			z-index: -1
			mask-composite: exclude
			inset: 0
			border-radius: inherit
			background: linear-gradient(86deg, #F27405 6.08%, #F2CD5C 49.51%, #F28705 91.6%)
			padding: 2px
			pointer-events: none
			content: ""
		&.btn-prev
			&::before
				content: '\f053'
		&.btn-next
			&::before
				content: '\f054'
				@apply font-awesome
		&:hover
			@apply bg-Primary-2
			&::before
				@apply text-Utility-white
			&::after
				@apply hidden
		&.white
			@apply border-2 border-white z-10
			&::before
				@apply text-white font-bold
			&::after
				@apply hidden
			&:hover
				@apply bg-white border-transparent 
				&::before
					@apply text-Primary-4
	// button chy icon sang tri
	&.btn-more
		@apply border-b border-b-white body-2
	&.btn-primary
		position: relative
		@apply rem:h-[40px] inline-flex items-center justify-center 
		@apply px-4 rounded-full transition-all duration-300
		@apply text-white bg-Primary-2 gap-2
		&::before
			content: ''
			position: absolute
			inset: 0
			border-radius: inherit
			background: linear-gradient(86deg, #F27405 6.08%, #F2CD5C 49.51%, #F28705 91.6%)
			opacity: 1
			transition: opacity 0.3s ease
			z-index: 1
		&:hover
			&::before
				opacity: 0
		.icon
			@apply relative z-2
			i
				@apply body-2
		span
			@apply relative z-2
		&.white
			@apply bg-white
			span
				@apply text-Primary-4
			&:hover
				@apply bg-Primary-2
				span
					@apply text-white