.footer
	@apply text-Primary-2
	.footer-wrapper
		.footer-column
			@apply border-r border-r-Primary-2/10 pr-10
			@apply last:border-r-0 last:pr-0
			.footer-menu
				@apply flex flex-col gap-3
				li
					a
						+hover-underline(95%, #fff)
						@apply body-2
					&:hover
						a
							@apply text-white
			.footer-contact
				.item
					a
						+hover-underline(95%, #fff)
						@apply hover:text-white
	.footer-bottom
		ul
			@apply flex items-center
			li
				&+li
					&::before
						content: '|'
						@apply text-Primary-2
						@apply mx-3
				&:hover
					a
						+hover-underline(95%, #fff)
						@apply text-white