.section-ProductList-1
	.bg-ProductList-1
		@apply bg-Secondary-2 bg-opacity-[0.08]
		.container
			.block-ProductList-1
				@apply flex-between
				@screen -md
					@apply flex flex-col gap-base
				.block-left
					@apply w-[calc(600/1400*100%)] flex flex-col gap-base
					@screen -md
						@apply w-full
					.content-product
						@apply body-1
				.block-right
					@apply w-[calc(690/1400*100%)]
					@screen -md
						@apply w-full
.section-ProductList-2
	.container
		.block-ProductList-2
			@apply grid grid-cols-12 gap-base
			@screen -lg
				@apply block
			.block-side-bar-sticky
				@apply  col-span-3
				.block-side-bar
					@apply  bg-white sticky rem:top-[calc(var(--header-height)+20px)]
					@screen -lg
						@apply fixed top-[calc(var(--header-height))] left-0 h-full rem:w-[280px] z-50 p-6
						@apply shadow-2xl transform -translate-x-full transition-transform duration-300
						&.active
							@apply translate-x-0
					ul
						@apply my-2 flex flex-col  gap-5 border-l border-l-Utility-gray-200
						@screen -lg
							@apply border-none pt-8
						li
							@apply heading-4 font-bold text-Utility-gray-700 pl-5  relative
							&::after
								content: ""
								@apply w-0 h-full bg-Primary-2 absolute top-0 -left-[1px] duration-100
							a
								@apply transition-300 ease-in-out pb-1 relative
								&::after
									content: ""
									@apply absolute bottom-0 left-0 w-0 h-[1px] transition-300 duration-300 ease-in-out bg-Primary-2
							&:hover,&.active
								@apply text-Primary-2
								&::after
									content: ""
									@apply w-[2px] h-full bg-Primary-2
								a
									&::after
										content: ""
										@apply absolute bottom-0 left-0 w-full h-[1px]
				.overlay-sidebar
					@apply fixed inset-0 bg-black bg-opacity-50 z-40 hidden transition-opacity duration-300
					&.active
						@apply block
			.block-products
				@apply col-span-9 flex flex-col gap-5
				.content-products
					h2
						@apply heading-4 font-bold text-Utility-gray-950 pb-5
				.block-card-products
					@apply grid grid-cols-2 gap-base
					@screen md
						@apply grid-cols-3
