.header
	@apply bg-white sticky left-0 right-0 z-100 top-0
	&::before
		content: ""
		background: linear-gradient(90deg, #F27405 23.86%, #F2CD5C 47.18%, #F28705 73.07%, #F29F05 94.87%)
		@apply absolute top-0 left-0 w-full pointer-events-none
		@apply rem:h-[6px]
	.header-wrapper
		@apply flex items-center justify-between 
		@apply h-25
		.header-logo
			@apply rem:w-[80px] rem:h-[80px]
			a
				@apply w-full h-full block
				img
					@apply w-full h-full object-contain
		.header-right
			@apply flex items-center gap-12 h-full
			.header-right-inner
				@apply flex items-center gap-3
				.header-search
					@apply inline-flex items-center justify-center cursor-pointer
					@apply rounded-full xl:sq-8 sq-12
					@apply bg-[#F27405] p-3 xl:p-2
					img, svg
						@apply w-full h-full object-contain
		.header-language
			@apply relative
			&:hover
				.header-language-active
					&::after
						@apply rotate-180
				.header-language-list
					// c ngn ng th m ra li dng ny
					// @apply opacity-100 pointer-events-auto
					.wpml-ls
						@apply p-0
			.header-language-active
				@apply flex items-center
				@apply rem:text-[16px]
				ul
					li
						a
							span
								@apply text-Primary-2
				&::after
					content: '\f107'
					@apply font-awesome font-light ml-1 
					@apply transition-all duration-300
				li:not(.wpml-ls-current-language)
					@apply hidden
				.wpml-ls 
					@apply p-0
		.header-language-list
			@apply absolute top-full left-0
			@apply  text-white bg-Primary-1 
			@apply p-1 py-1 rem:text-[14px]
			@apply opacity-0 transition-300  pointer-events-none 
			// @apply shadow-dropshadow-light
			li
				a
					+hover-underline(95%, #fff)
				&.wpml-ls-current-language
					@apply hidden
	.header-menu
		@apply h-full flex
		@screen xl
			ul.header-nav
				@apply flex justify-center items-center gap-11
				>li							
					@apply relative h-full  
					@apply inline-flex flex-col justify-center
					a						
						@apply inline-flex items-center
						@apply rem:text-[18px] font-bold uppercase
						@apply border-b border-transparent text-Primary-2
						i
							@apply pb-1
					&:hover
						&>a
							@apply text-Primary-1 
							@apply border-b border-Primary-1
						&>ul
							@apply opacity-100 pointer-events-auto
					&[class*='current']
						&>a
							@apply text-Primary-1 
							@apply border-b border-Primary-1
					&.menu-item-has-children
						&>a
							&::after
								content: '\f0d7'
								@apply font-awesome font-bold rem:text-[12px] ml-2
					&>ul
						@apply rounded-b-5 w-max
						@apply flex flex-col gap-6
						@apply top-full left-0 absolute p-6
						@apply bg-white border-b-3 border-b-Primary-4 shadow-Shadow-4
						@apply opacity-0 transition-all pointer-events-none
						&>li
							a
								@apply rem:text-[18px] font-normal inline-block text-Utility-gray-950 normal-case
							&:hover
								a
									@apply text-Primary-1
		@screen -xl
			height: calc(100dvh - var(--header-height))					
			@apply fixed top-[var(--header-height)] right-0 w-[80%]  z-[100]
			@apply flex flex-col justify-between px-[15px] overflow-y-auto
			@apply opacity-0 translate-x-full transition-all duration-700 bg-white
			&>ul.header-nav
				@apply flex flex-col gap-3 mt-[26px]
				li
					@apply relative
					a
						@apply text-[18px] font-medium  block pb-0
					&.active
						.icon-arrow
							&::before
								@apply rotate-180
					.menu-item
						@apply flex items-center relative
					.icon-arrow
						@apply absolute top-0 right-0 h-full 
						@apply px-1 text-3xl font-normal
						@apply flex-center
						&::before
							content: '\f078'
							transform-origin: center center
							will-change: transform
							@apply font-awesome text-[20px] font-light ml-2
							@apply transition-300
					&.menu-item-has-children				
						&>a
							@apply relative 
							@apply flex items-center justify-between
							&.dropdown-active
								&::after
									@apply rotate-180
							&::after
								content: '\f0d7'
								@apply font-awesome font-black text-lg
						>ul
							@apply hidden bg-Primary-1 mt-1
							>li
								>a
									@apply border-none p-2 text-white
								&[class*='current']
									&>a
										@apply text-black
		
	.header-bar
		@apply sq-12 flex-shrink-0 
		@apply items-center justify-center xl:hidden flex
		@apply  bg-Primary-4 text-2xl  text-white rounded-full
		&.active
			i
				&::before
					content: "\f00d"
.header-overlay
	position: fixed
	opacity: 0
	transition: 0.3s all ease-in-out
	pointer-events: none
	top: 0
	left: 0
	width: 100%
	height: 100%
	background-color: rgba(0, 0, 0, 0.5)
	z-index: 50
body
	&.show-cart
		.header-overlay
			@apply opacity-100 pointer-events-auto
body.isOpenMenu
	.header-menu
		@apply opacity-100 translate-x-0 #{!important}
	.header-overlay
		@apply opacity-100 pointer-events-auto
body.isOpenSearch
	.header-search-form
		@apply opacity-100 pointer-events-auto translate-y-0