.home-2
	.wrap-list
		.item
			@apply relative flex-center
			&:last-child
				@screen -lg
					@apply col-span-full h-[13.333333333333334rem]
				@screen -md
					@apply h-auto col-span-1
			.circle
				@apply rem:w-[256px] rem:h-[256px]
				img
					@apply w-full h-full object-contain
			.content
				@apply absolute md:top-[36%] top-[40%] left-2/4 -translate-x-2/4 -translate-y-2/4
				@apply text-center w-full
				.number
					text-shadow: 0 4px 4px rgba(31, 34, 39, 0.08)
					@apply md:rem:text-[60px] rem:text-[40px] text-Primary-3 font-black tabular-nums