.home-1
	.swiper
		.swiper-slide
			.home-1-banner
				@apply relative
				&::before
					content: ''
					background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 47.44%, rgba(0, 0, 0, 0.60) 100%)
					@apply absolute top-0 left-0 z-1
					@apply w-full h-full
					@apply pointer-events-none
				@screen -md
					@apply h-screen
				>a
					@screen -md
						@apply h-full
					@screen -lg
						@apply pt-[62%]
				.home-1-content
					@apply absolute  left-2/4 -translate-x-2/4 bottom-32 z-2
					@apply w-full px-10
	.arrow-button
		@apply absolute left-2/4 -translate-x-2/4 z-2 bottom-15
	.wrap-button-slide
		.btn-prev
			@apply xl:mr-[-10rem] #{!important}
		.btn-next
			@apply xl:ml-[-10rem] #{!important}