.section-ProductDetail-1
	.block-ProductDetail-1
		.product-detail-1
			.swiper-main
				@apply mb-6
				.swiper-wrapper
					.swiper-slide
						.img
							@apply bg-Primary-2 bg-opacity-[0.08] rounded-4
			.thumbs-wrapper
				@apply section-px relative
				.swiper-thumbs
					.swiper-wrapper
						.swiper-slide
							@apply border-2 border-transparent
							.img
								@apply bg-Primary-2 bg-opacity-[0.08] rounded-4
						.swiper-slide-thumb-active
							@apply  border-Primary-1 rounded-4
				.wrap-button-slide
					.btn
						@apply absolute top-1/2 -translate-y-1/2 m-0 z-10
						@screen -lg
							@apply static flex mt-base
						&.btn-prev
							@apply left-0 right-auto
						&.btn-next
							@apply right-0 left-auto
.section-ProductDetail-2
	.product-detail-2-list
		.wrap
			&>ul
				@apply flex-center gap-base whitespace-nowrap overflow-x-auto
				@screen -md
					@apply  px-[15px] justify-start items-start
				li
					@apply body-1
					a
						@apply py-3 relative text-Utility-gray-500
						@apply duration-200 ease-in-out inline-block  outline-none
						&:hover
							@apply text-Primary-3
						&::after
							content: ""
							@apply absolute w-0 h-[1px] duration-200 ease-in-out bg-Primary-3 bottom-0 left-0
						&:hover
							&::after
								@apply w-full
					&.active
						a
							@apply text-Primary-3 font-bold
							&::after
								@apply w-full
			.item-tablet
				@apply bg-Primary-1 bg-opacity-[0.08]
				.item-content
					@apply section-py
					@screen lg
						@apply py-16
						.tabslet-content
							.format-content
								@apply body-1
								p
									@apply mb-3
									@screen lg
										@apply mb-5
								ol
									li
										@apply heading-4
.section-ProductDetail-3
	.product-detail-3
		@apply bg-Primary-2 bg-opacity-[0.08] relative
		.swiper-column-auto
			.wrap-button-slide
				.btn
					@apply absolute-center-y m-0 z-10
					@screen -lg
						@apply static flex mt-base
					&.btn-prev
						@apply left-[9%] right-auto
					&.btn-next
						@apply right-[9%] left-auto
