.about-5
	@screen -md
		@apply mb-10
	.thumb
		@apply overflow-hidden relative
		.swiper
			@apply w-[120px] sm:w-[150px] md:rem:w-[300px] overflow-visible
		.wrap-slide-years
			@apply relative
			&::before
				content: ''
				@apply absolute rem:top-[14px]  -translate-y-1/2 left-0 w-full h-[1px] bg-Utility-gray-300
			.swiper-slide	
				.item-history
					@apply gap-2
				.content
					.number
						@apply text-xl font-semibold text-Utility-gray-300
				.line-year
					.circle
						@apply bg-white border border-transparent rem:w-[28px] rem:h-[28px]
						@apply flex items-center justify-center cursor-pointer transition-all ease-in-out
						.dot
							@apply bg-Utility-gray-300 w-[16px] h-[16px] rounded-full 
				&.swiper-slide-next
					.wrap-content
						.title-48
							@apply text-Primary-1
					.content
						.line-year
							.dot
								@apply bg-Primary-2
			.swiper-slide-thumb-active
				.content
					.number
						@apply text-Primary-2 font-bold
				.line-year
					.circle
						@apply bg-white border-Primary-2
						.dot
							@apply bg-Primary-2
	.main
		.swiper
			@apply -m-3 md:mb-5
			.swiper-slide
				@apply p-3
				.item
					@apply items-center
					.col-right
						.format-content
							@apply rem:max-h-[380px] overflow-auto pr-4
							ul
								@apply gap-6
							h3
								@apply body-2
	.wrap-button-slide
		.btn-prev
			@apply ml-[-4rem] #{!important}
		.btn-next
			@apply mr-[-4rem] #{!important}
		&.drop
			@apply mt-0 #{!important}