.home-3
	.swiper-column-auto
		--spv: 2
		--mr: calc(12/1920*100rem)
		@screen sm
			--spv: 2
			--mr: calc(16/1920*100rem)
		@screen md
			--spv: 2
			--mr: calc(20/1920*100rem)
		@screen lg
			--spv: 3
			--mr: calc(20/1920*100rem)
		@screen xl
			--spv: 4
			--mr: calc(20/1920*100rem)
		.swiper-slide
			@screen md
				--image-height: #{rr(388px)}
			--button-height: 2.5rem
			--padding-bottom: #{rr(12px)}
			@apply h-auto #{!important}
			.item
				@apply flex flex-col h-full pb-3
				@apply xl:rem:min-h-[520px] bg-white rounded-5 md:px-6 md:pt-6 p-3
				@apply border-b-2 border-b-transparent
				@screen xl
					&:hover
						@apply border-b-Primary-2
						.image-wrapper
							@apply h-[calc(var(--image-height)_-_var(--button-height)_-_var(--padding-bottom))] #{!important}
				.image-wrapper
					@apply transition-300
					@apply h-[var(--image-height)] #{!important}
				&:hover
					@apply border-Primary-2
					.content
						.desc
							@apply translate-y-0
							@apply opacity-100
				.content
					@apply pt-4
					@apply transition-300 w-full flex flex-col relative flex-1 gap-5
					.desc
						@apply opacity-100 max-h-[none] overflow-visible
						@screen xl
							@apply transition-300
							@apply flex-center
							@apply absolute left-0 w-full lg:bottom-[0.5rem]
							@apply translate-y-full opacity-0
					a.btn-primary
						@apply rem:h-[var(--button-height)]
						span
							@apply text-lg
					>.btn
						@apply translate-y-0
