@layer components
	*::-webkit-scrollbar
		@apply w-[calc(4/1920*100rem)] h-[calc(4/1920*100rem)]
	*::-webkit-scrollbar-track
		background-color: rgba(#e5e5e5,.5 )
	*::-webkit-scrollbar-thumb
		@apply bg-Primary-1
	.indicator-swipe
		background: rgba(40, 53, 147, 0.20)
		@apply z-30 px-3
		@apply w-[calc(144/19.2*1rem)] h-14 rounded-full
		@apply flex justify-between items-center
		@apply transition-all pointer-events-none
		&:not(.active)
			@apply opacity-0
		svg
			@apply stroke-white
		.hand
			animation: moveSwipeGesture 2.25s infinite ease-in-out
			@apply md:w-16 md:h-12 w-10 h-10
	.nav-primary
		@apply flex items-center justify-center gap-2
		@apply overflow-auto whitespace-nowrap
		@screen -md
			@apply justify-normal pb-3
		li
			a
				@apply inline-flex items-center justify-center 
				@apply px-6 h-11 rounded-full border border-Primary-2
				@apply bg-white text-Primary-2 font-semibold uppercase
			&.active, &:hover
				a
					@apply bg-Primary-2 text-white
	.pagination
		@apply flex items-center justify-center
		ul
			@apply flex items-center justify-center gap-6
			li
				a, span
					@apply inline-flex items-center justify-center
					@apply sq-12 rounded-1
					@apply bg-transparent border-2 border-Utility-gray-500
					@apply  heading-4 font-bold text-Utility-gray-500
				&.active, &:hover
					a, span
						@apply bg-Primary-1 text-white border-transparent
	.play-icon
		@apply inline-flex items-center justify-center
		@apply absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2
		@apply text-white bg-Primary-1
		@apply sq-12 rounded-full
	.page-banner-main
		@apply relative
		&::before
			content: ''
			background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 47.44%, rgba(0, 0, 0, 0.60) 100%)
			@apply absolute top-0 left-0 w-full h-full z-1 pointer-events-none
		.content
			@apply absolute bottom-0 left-2/4 -translate-x-2/4 z-1
			@apply w-full px-10 py-15 text-center
			.global-breadcrumb
				@apply bg-transparent
				p
					@apply h-full gap-2 text-center justify-center
					a,span
						@apply text-white body-4
	.service-item
		@apply cursor-pointer
		.img
			@apply overflow-hidden 
			@screen -xl
				@apply rounded-b-none
			img
				@apply transition-transform duration-300 ease-out
				@apply scale-100
		.content
			background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 0%, rgba(0, 0, 0, 0.60) 100%)
			@apply absolute top-2/4 left-2/4 -translate-x-2/4 -translate-y-2/4 
			@apply xl:rem:w-[612px] xl:rem:h-[576px] rem:w-[500px] rem:h-[424px] rounded-5 p-5
			@apply transition-all duration-500
			@screen -xl
				background: linear-gradient(135deg, rgba(242, 116, 5, 0.80) 17.34%, rgba(242, 160, 49, 0.79) 39.58%, rgba(242, 205, 92, 0.56) 69.48%, rgba(242, 135, 5, 0.40) 90.9%)
				@apply static w-full h-full transform-none rounded-t-none
		.content-inner
			@apply opacity-0 translate-y-5 max-h-0 overflow-hidden
			@apply transition-all duration-500 ease-in-out
			@screen -xl
				@apply opacity-100 transform-none max-h-full
		@screen xl
			&:hover
				.img
					img
						@apply scale-110
				.content
					background: linear-gradient(135deg, rgba(242, 116, 5, 0.80) 17.34%, rgba(242, 160, 49, 0.79) 39.58%, rgba(242, 205, 92, 0.56) 69.48%, rgba(242, 135, 5, 0.40) 90.9%)
					backdrop-filter: blur(6px)
				.content-inner
					@apply opacity-100 translate-y-0 max-h-[500px]
	.card-product
		.img
			@apply rounded-4 bg-Primary-2 bg-opacity-[0.08]
		.content-card-product
			@apply py-4 relative
			&::before
				content: ""
				@apply absolute left-0 bottom-0 w-full h-[2px] bg-linear-2
			&::after
				content: ""
				@apply absolute left-0 bottom-0 w-full h-[2px] bg-Utility-gray-200 transition-all duration-300
				@apply heading-4 font-bold duration-300
		&:hover
			.content-card-product::after
				@apply opacity-0
		.title-card-product
			@apply heading-4 font-bold duration-300
	.slide-service
		.swiper-column-auto
			.swiper-slide
				--spv: 2
				@screen sm
					--spv: 1
				@screen md
					--spv: 3
				@screen lg
					--spv: 4
					--mr: calc(40/1920*100rem)