.about-6
	.infos
		.about-6-table
			@apply border border-Utility-gray-200
			table
				@apply table-fixed w-full border-separate border-spacing-0
			thead
				@apply bg-Primary-1
				tr
					td
						@apply border-r border-r-gray-200 last:border-r-0 text-white  font-bold heading-5
						@apply px-4 rem:h-[68px]
			tbody
				tr:nth-child(even)
					@apply bg-white px-3 py-5
				tr
					@apply bg-[#EEEFF6]
					td
						@apply lg:rem:h-[88px] h-10 px-4 border-r border-r-Utility-gray-200 border-b border-b-Utility-gray-200 body-1 
						>a
							@apply hover:text-Primary-2 
