.leader
	ul.tabslet-tab
		@apply flex justify-center gap-base xl:mb-20 mb-base
		li
			a
				@apply body-1 font-bold
				@apply inline-flex
				@apply pb-1 border-b border-transparent
			&:hover, &.active
				a
					@apply border-b border-Primary-2 text-Primary-2
	.tabslet-content
		.column-list
			.row
				> [class*="col-"]
					@screen xl
						padding-right: clamp(50px,calc(50/1920*100rem),calc(50/1920*100rem))
						padding-left: clamp(50px,calc(50/1920*100rem),calc(50/1920*100rem))
