.section-PhatTrien-1
	@screen -lg
		background-position: right !important
		@apply relative
		&::after
			content: ""
			@apply absolute top-0 left-0 w-full h-full bg-black bg-opacity-[0.4] z-0 backdrop-blur-md
	.wrap-content
		@apply rem:max-w-[718px] rem:min-h-[740px] w-full -lg:max-w-full flex-center flex-col relative z-10
		h1
			@screen -lg
				@apply text-Primary-1
		&>.format-content
			@apply mt-base body-1
			@screen -lg
				@apply text-white
.section-PhatTrien-2
	@apply rem:min-h-[790px] section-py
	.wrap-content
		@apply flex-center flex-col gap-base
		.format-content
			@apply body-1 text-center
		ul
			@apply flex-center  gap-base flex-wrap
			li
				@apply body-2 text-center p-4  bg-Primary-2 text-white rounded-4 rem:max-w-[372px]
.section-PhatTrien-3
	.container
		.wrap-content
			@apply grid grid-cols-2 gap-base -md:grid-cols-1
			.block-left
				@apply flex-center flex-col gap-base
				&>.format-content
					@apply body-1
					ul
						li
							@apply flex items-center gap-2
							&::before

								content: ""
								background-image: url('../img/icon-phatrien.svg')
								@apply w-[24px] h-[3px] bg-contain bg-center bg-no-repeat z-10
