.tool-fixed-cta
	@apply fixed bottom-[5vh] right-0 z-50 flex flex-col gap-1
	@apply items-end rem:w-[52px]
	.btn
		&.button-to-top
			box-shadow: 0 4px 40px 0 rgba(0, 0, 0, 0.04)
			@apply bg-Utility-gray-200 rounded-l-2
			.icon
				animation: none !important
				@apply sq-10  flex-center
				@apply relative p-2 overflow-hidden
				&:hover
					&::after
						@apply translate-y-0 opacity-100
					&::before
						@apply translate-y-[-100%]
				&::after
					@apply translate-y-[100%] opacity-0
				&::before,&::after
					content: '\f062'
					@apply font-awesome text-lg  font-light transition-300 absolute text-Primary-2
	.btn
		transform: translateX(calc(100% - 2.7083333333rem))
		@apply h-12 flex rounded-r-none
		@apply w-fit transition-300 rounded-l-2
		&.btn-social
			@apply bg-Primary-2
			.content
				ul
					@apply gap-2
					li
						img
							@apply max-w-none #{!important}
						a
							@apply sq-8 inline-flex items-center justify-center
							i
								@apply font-normal
		&:hover
			transform: translateX(0)
		.btn-icon
			@apply h-full flex-center rem:w-[52px]
			.icon
				animation: ring 1s ease-in-out infinite both
				@apply text-Utility-white
				i
					@apply font-light body-1
		.content
			@apply h-full flex items-center
			@apply pr-3 font-semibold text-white py-2 whitespace-nowrap
			ul
				@apply flex items-center gap-2 h-full
				@apply border-l border-white/40 pl-2
				
